"use client";
//React and Next Import
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
//Utils
import { EmailValidator } from "@/utilities/regex";
import { sellerRegister } from "@/utilities/url";
import { GetBussinessName } from "@/utilities/localStorage";
//Components

export default function SellerSelling() {
  const { t } = useTranslation();
  const [email, setEmail] = useState("");
  const [mailerror, setMailError] = useState("");
  const [check, setCheck] = useState(false);
    let bussiness = GetBussinessName();

  const handleemail = () => {
    setCheck(true);
    if (validate()) {
      setEmail("");
      setCheck(false);
      let url = sellerRegister;
      window.open(url);
    }
  };

  useEffect(() => {
    if (check == true) {
      validate();
    }
  }, [email]);
  const validate = () => {
    let ValidateObj = {
      email: true,
    };
    if (email == "") {
      setMailError(t("validationMessage.emailEmpty"));
      ValidateObj.email = false;
    } else {
      if (EmailValidator(email)) {
        setMailError("");
        ValidateObj.email = true;
      } else {
        setMailError(t("validationMessage.incorrectEmail"));
        ValidateObj.email = false;
      }
    }

    if (ValidateObj.email == true) {
      return true;
    } else {
      return false;
    }
  };
  return (
    <div className="grid items-center grid-cols-1 gap-16 m-auto sm:gap-32 sm:grid-cols-2 mb-28">
      <div>
        <h3 className="mb-5 text-4xl font-black text-black-300">
          {t("myprofile.CompleControl")}
        </h3>
        <p className="mb-8 text-sm font-normal text-black-300">
          {t("myprofile.hassleFreeSelling")} <span className="font-black"> #{bussiness}</span>
        </p>
        <div className="relative">
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className={`border border-slate-300 rounded-md h-12 px-4 text-base text-black placeholder:text-grey-400 w-full`}
            placeholder={t("signin.EmailId")}
          />
          <button
            disabled={email == ""}
            className={` cursor-pointer absolute top-0 btn rtl:right-[unset] rtl:left-0 right-0 px-6 h-12 bg-boldblack hover:bg-zinc-800 text-white rounded ${
              email == "" && "opacity-20"
            }`}
            onClick={handleemail}
          >
            {t("myprofile.StartSell")}
          </button>
          {check == true && mailerror !== "" && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {mailerror}
            </p>
          )}
        </div>
      </div>
      <div>
        <img src="/img/seller-img-1.png" alt="seller-img-1" />
      </div>
    </div>
  );
}
