// React & Next
import { useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";
import Link from "next/link";

// Third Party
import { useTranslation } from "react-i18next";

//Components
import ContactSeller from "../../ContactSeller";
import { GetCustomerProfile } from "@/utilities/localStorage";
import ToastMessage from "@/Components/ToastMessage";

// Api
import { ContactAdminApi } from "@/api/clientsideaction/buyerapi";

export default function SellerSupport({token}) {
  // Translation
  const { t } = useTranslation();
  
  // Router
  const router = useRouter();
  
  // Local storage data
  const customerProfile = GetCustomerProfile();

  const [contactSeller, setContactSeller] = useState(false);
  const [sellerName, setSellerName] = useState("");
  const [sellerCompanyName, setSellerCompanyName] = useState("");
  const [contactSellerDescripition, setContactSellerDescripition] =
    useState("");
  const [contactSellerDescripitionError, setContactSellerDescripitionError] =
    useState("");
  const [attachmentFile, setAttachmentFile] = useState([]);
  const [contactSellerSubmit, setContactSellerSubmit] = useState(false);

  useEffect(() => {
    if (contactSellerSubmit) {
      contactSellerValidation();
    }
  }, [contactSellerDescripition]);

  const contactSellerValidation = () => {
    const obj = {
      description: true,
    };
    if (contactSellerDescripition !== "") {
      setContactSellerDescripitionError("");
      obj.description = true;
    } else {
      setContactSellerDescripitionError(
        t("validationMessage.descriptionEmpty"),
      );
      obj.description = false;
    }
    return obj.description;
  };

  const handleContactSellerChange = (e) => {
    setContactSellerDescripition(e.target.value.trimStart());
  };

  const handleAddAttachment = (event) => {
    let addFile = [...attachmentFile];
    const file = event.target.files[0];
    if (file.name?.match(/\.(pdf)$/)) {
      const reader = new FileReader();
      reader.onloadend = () => {
        let obj = {
          file: reader.result,
          fileName: file.name,
        };
        addFile.push(obj);
        setAttachmentFile(JSON.parse(JSON.stringify(addFile)));
      };
      reader.readAsDataURL(file);
    } else {
      ToastMessage({
        type: "error",
        message: t("toastMessage.addAttachmentFile"),
      });
    }
  };

  const handleRemoveFile = (index) => {
    attachmentFile.splice(index, 1);
    setAttachmentFile([...attachmentFile]);
  };

  const handleSubmitContactSeller = async () => {
    setContactSellerSubmit(true);
    if (contactSellerValidation()) {
      setContactSellerDescripitionError("");
      const obj = {
        firstName: customerProfile.firstName,
        lastName: customerProfile.lastName,
        emailId: customerProfile.email,
        files: attachmentFile,
        userRequirements: contactSellerDescripition,
      };
      ContactAdminApi(obj, closeContactSeller);
    }
  };

  const closeContactSeller = () => {
    setAttachmentFile([]);
    setContactSellerDescripition("");
    setContactSellerSubmit(false);
    setContactSeller(false);
    setContactSellerDescripitionError("");
  };

  const handleContactSeller = () => {
    if (token) {
      let bussinessName = localStorage.getItem("bussinessName");
      setContactSeller(true);
      setSellerName(bussinessName);
      setSellerCompanyName("");
    } else {
      router.push("/buyer-login");
    }
  };
  
  return (
    <div className="bg-light-green">
      <div className="flex flex-col items-center px-5 pt-10 mx-auto mb-20 lg:px-0 pb-7 max-w-screen-2xl">
        <h3 className="mb-3 text-xl font-medium text-dark-green">
          {t("myprofile.Support")} 24/7
        </h3>
        <p className="mb-8 text-base font-normal text-center text-black-300">
          {t("myprofile.supportanytime")}{" "}
        </p>
        {token == (undefined || null) ? (
            <Link
              prefetch={false}
              href={"/buyer-login"}
              className="flex items-center justify-center w-full h-12 p-4 text-base font-medium text-white bg-black rounded hover:bg-zinc-800 sm:w-80 btn cursor-pointer"
            >
              {t("myprofile.ContactUsany")}
            </Link>
          ) : (
            <Link
              prefetch={false}
              href=""
              className="flex items-center justify-center w-full h-12 p-4 text-base font-medium text-white bg-black rounded hover:bg-zinc-800 sm:w-80 btn cursor-pointer"
              onClick={handleContactSeller}
            >
              {t("myprofile.ContactUsany")}
            </Link>
          )}

        {/* <button className="flex items-center justify-center w-full h-12 p-4 text-base font-medium text-white bg-black rounded cursor-default hover:bg-zinc-800 sm:w-80 btn">
          {t("myprofile.ContactUsany")}
        </button> */}
      </div>

      {contactSeller == true && (
        <ContactSeller
          contactSeller={contactSeller}
          setContactSeller={setContactSeller}
          sellerEmail={sellerName}
          handleContactSellerChange={handleContactSellerChange}
          handleAddAttachment={handleAddAttachment}
          attachmentFile={attachmentFile}
          handleRemoveFile={handleRemoveFile}
          customerProfile={customerProfile}
          handleSubmitContactSeller={handleSubmitContactSeller}
          contactSellerDescripition={contactSellerDescripition}
          contactSellerDescripitionError={contactSellerDescripitionError}
          closeContactSeller={closeContactSeller}
          sellerCompanyName={sellerCompanyName}
        />
      )}
    </div>
  );
}
